$layout-mobile: 667px;

.font-face-quiny {
  font-family: "Quiny";
}


.App {
  text-align: center;
  background-image: url(../images/bckg.jpg);
  height: 100vh;
  object-fit: contain;
  max-height: 100vh;
  width: 100vw;
  max-width: 100vw;
  background-size: cover;
  background-position: 67%;

  .title{
    font-size: 50px;
    text-transform: uppercase;
    color: #ff7c34;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    top: 45%;
    letter-spacing: -2px;
    width: 100%;
    &:hover{
      color: black;
    }
    span:first-child{
      margin-right: -10px;
    }
  }
  @media (min-width: $layout-mobile) {
    background-position: right;
    .title{
      font-size: 70px;
      width: auto;
    }
  }
}


